import { useCallback, useEffect, useState } from "react";
import axios from "axios";
import clsx from "clsx";
import { CheckIcon, TrashIcon } from "@heroicons/react/24/outline";
import { BACKEND_API_PATH, BACKEND_PATH } from "@config";
import { APPLY_CODE_STATUS } from "@constants";
import { sliderToMonths, CopyToClipboard } from "@helper";
import { useToast } from "@contexts/ToastContext";
import useStore from "@store";
import { Horizontal3DotsLoading } from "@assets/loader";
// import { QUIZZES } from "@constants/index";

const ApplicationItem = ({ apply, index }) => {
  const { addToast } = useToast();
  const [expand, setExpand] = useState(false);
  const { deleteApplication } = useStore();

  const handleSetExpand = async () => {
    try {
      if (!expand && apply.updated) {
        await axios.patch(`${BACKEND_API_PATH}/applications/${apply._id}`);
      }
    } catch (error) {
      console.log(error);
    } finally {
      setExpand(!expand);
    }
  };

  const handleClickCopyEmail = (e) => {
    e.stopPropagation();
    CopyToClipboard(apply.user.email);
    addToast({ type: "success", message: "Email copied" });
  };

  const handleApproveStatus = async (e) => {
    try {
      e.stopPropagation();
      if (window.confirm("Approve camera to this user?")) {
        await axios.get(
          `${BACKEND_API_PATH}/applications/camera/${apply.token}`
        );
        // await axios.patch(`${BACKEND_API_PATH}/applications/status/${apply._id}`, {
        //   hasRunStatus: true
        // });
        addToast({ type: "success", message: "Camera approved" });
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleDelete = async (e) => {
    try {
      e.stopPropagation();
      if (
        window.confirm(
          "Do you really delete this application? Once you click OK, all data related this application will be removed."
        )
      ) {
        await axios.delete(
          `${BACKEND_API_PATH}/applications/token/${apply.token}`
        );
        deleteApplication(apply);
        addToast({ type: "success", message: "Application deleted" });
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div
      className={clsx(
        "border border-indigo-500 rounded-xl px-4 py-2 transition-all relative",
        {
          "bg-gray-50": !apply.updated,
          "bg-red-100": apply.updated,
        }
      )}
    >
      <div
        className="flex justify-between items-center gap-4 cursor-pointer"
        onClick={handleSetExpand}
      >
        <div className="w-full flex items-center gap-4">
          {apply.user && (
            <div className="min-w-52">
              <p className="pl-2 text-lg font-semibold border-b border-indigo-500">
                {apply.user.fullName}
              </p>
              <p
                title={apply.user.email}
                className="bg-indigo-100 text-indigo-900 text-sm cursor-pointer w-full truncate"
                onClick={handleClickCopyEmail}
              >
                {apply.user.email}
              </p>
            </div>
          )}
          <div className="hidden sm:block min-w-28 max-w-28">
            <p className="border-b border-indigo-500 w-full truncate">
              {apply.location_country || "..."}
            </p>
            <p className="text-xs text-gray-600">country</p>
          </div>
          <div className="hidden md:block min-w-28">
            <p className="border-b border-indigo-500">{apply.step}</p>
            <p className="text-xs text-gray-600">last step</p>
          </div>
          <div className="w-full hidden lg:flex justify-between gap-4">
            {/* <div className="flex flex-wrap gap-2 items-center">
              {apply.selectedRoles &&
                Array.isArray(apply.selectedRoles) &&
                apply.selectedRoles.map((item, index) => (
                  <div
                    className="bg-indigo-100 border border-indigo-500 px-2 text-sm rounded-full"
                    key={index}
                  >
                    {item}
                  </div>
                ))}
            </div> */}
            <div>
              <div className="w-16 h-16 border border-indigo-300 rounded-full flex items-center justify-center overflow-hidden">
                <div className="h-full w-full">
                  <p
                    className={clsx(
                      "h-2/3 flex items-center justify-center text-white font-bold",
                      {
                        "bg-red-500": Number(apply.status) === 0 && !apply.hasRunStatus,
                        "bg-yellow-600": Number(apply.status) === 1 && !apply.hasRunStatus,
                        "bg-blue-600": Number(apply.status) === 2 && !apply.hasRunStatus,
                        "!bg-green-500": apply.hasRunStatus,
                      }
                    )}
                  >
                    {/* {apply.status === 2 && apply.cameraStatus
                      ? "RUN"
                      : APPLY_CODE_STATUS[apply.status]} */}
                    {apply.hasRunStatus
                      ? "RUN" // Permanent RUN text
                      : APPLY_CODE_STATUS[apply.status]}
                  </p>
                  <p className="h-1/3 text-center text-xs border-t border-indigo-300">
                    status
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="block min-w-56">
          <div className="flex gap-1">
            {apply.cameraStatus && (
              <div className="border rounded bg-green-700 text-white">
                Camera approved
              </div>
            )}
            <button
              className="border px-2 rounded bg-blue-600 text-white"
              onClick={handleApproveStatus}
            >
              <CheckIcon className="size-6" />
            </button>
            <button
              className="border px-2 bg-red-500 text-white rounded"
              onClick={handleDelete}
            >
              <TrashIcon className="size-6" />
            </button>
          </div>
          <div className="flex justify-between items-baseline">
            <small className="mr-2">Started:</small>
            {new Date(apply.createdAt).toLocaleString()}
          </div>
          <div className="flex justify-between items-baseline">
            <small className="mr-2">Ended:</small>
            {new Date(apply.updatedAt).toLocaleString()}
          </div>
        </div>
      </div>
      {expand && <ExpandPart token={apply.token} />}
      <div className="absolute top-0 left-0 border-r border-b border-indigo-500 rounded-br-xl text-xs font-medium w-12 text-center">
        {index + 1}
      </div>
    </div>
  );
};

export default ApplicationItem;

const MainSkills = ({ skills }) => {
  const [show, setShow] = useState(false);
  const [expand, setExpand] = useState(false);

  useEffect(() => {
    setShow(skills.length > 1);
  }, [skills]);

  const handleSetExpand = useCallback(() => setExpand(!expand), [expand]);

  return (
    <div>
      {skills &&
        Array.isArray(skills) &&
        skills.map(
          (item, index) =>
            (expand ? true : index < 1) && (
              <div key={index}>
                <span className="mr-1">
                  {expand && `${index + 1}.`} {item.name}:
                </span>
                <span className="mr-1">{item.years}years,</span>
                <span className="">{item.level}</span>
              </div>
            )
        )}
      {show && (
        <div className="flex justify-end">
          <span
            onClick={handleSetExpand}
            className="-mt-3 cursor-pointer bg-[#0004] hover:bg-gray-600 text-[#000a] hover:text-white text-sm font-bold px-3 rounded-full transition-all"
          >
            {expand ? "Less" : "More"}
          </span>
        </div>
      )}
    </div>
  );
};

const ExpandPart = ({ token }) => {
  const [loading, setLoading] = useState(true);
  const [apply, setApply] = useState(null);
  const [video, setVideo] = useState(null);
  // const [quizzes, setQuizzes] = useState([]);

  const getApplicationData = useCallback(async () => {
    try {
      const response = await axios.get(
        `${BACKEND_API_PATH}/applications/all/token/${token}`
      );
      console.log(response.data);

      setApply(response.data.application);
      setVideo(response.data.video);
      // setQuizzes(response.data.quizzes);
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  }, [token]);

  useEffect(() => {
    const timer = setTimeout(() => {
      getApplicationData();
    }, 300);
    return () => {
      if (timer) clearTimeout(timer);
    };
  }, [getApplicationData]);

  return (
    <div className="border-t border-indigo-300 grid sm:grid-cols-2 md:grid-cols-3 mt-2 pt-2">
      {loading ? (
        <>
          <div></div>
          <div className="flex justify-center">
            <Horizontal3DotsLoading width={80} color="#6366f1" />
          </div>
          <div></div>
        </>
      ) : (
        apply && (
          <>
            <div className="p-2">
              <table>
                <tbody>
                  {apply.user && (
                    <>
                      <tr>
                        <td>Name</td>
                        <td>{apply.user.fullName}</td>
                      </tr>
                      <tr>
                        <td>email</td>
                        <td>{apply.user.email}</td>
                      </tr>
                    </>
                  )}
                  <tr>
                    <td>Job Search</td>
                    <td>{apply.jobSearchStatus}</td>
                  </tr>
                  <tr>
                    <td>country</td>
                    <td>{apply.location_country}</td>
                  </tr>
                  <tr>
                    <td>state</td>
                    <td>{apply.location_state}</td>
                  </tr>
                  <tr>
                    <td>city</td>
                    <td>{apply.location_city || "..."}</td>
                  </tr>
                  <tr>
                    <td>will move</td>
                    <td>{apply.location_isMoving ? "Yes" : "No"}</td>
                  </tr>
                  <tr>
                    <td>English</td>
                    <td>{apply.englishLevel}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div className="p-2">
              <table>
                <tbody>
                  {/* <tr>
                    <td>Role</td>
                    <td>
                      {apply.selectedRoles &&
                        Array.isArray(apply.selectedRoles) &&
                        apply.selectedRoles.map((item, index) => (
                          <div key={index}>{item}</div>
                        ))}
                    </td>
                  </tr>
                  <tr>
                    <td>Skills</td>
                    <td>
                      <MainSkills skills={apply.mainSkills} />
                    </td>
                  </tr> */}
                  {/* <tr>
                    <td>Relevant Skills</td>
                    <td>
                      <MainSkills skills={apply.relevantSkills} />
                    </td>
                  </tr> */}
                  {/* <tr>
                    <td>Experience</td>
                    <td>
                      <Experience exp={apply.experience} />
                    </td>
                  </tr> */}
                  <tr>
                    <td>rate/m</td>
                    <td>${apply.monthlyRate}</td>
                  </tr>
                  <tr>
                    <td>create</td>
                    <td>{new Date(apply.createdAt).toLocaleString()}</td>
                  </tr>
                  <tr>
                    <td>update</td>
                    <td>{new Date(apply.updatedAt).toLocaleString()}</td>
                  </tr>
                  <tr>
                    <td>status</td>
                    <td>{APPLY_CODE_STATUS[apply.status]}</td>
                  </tr>
                  <tr>
                    <td>camera</td>
                    <td>{apply.cameraStatus ? "Yes" : "No"}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div className="p-2">
              <table>
                <tbody>
                  <tr>
                    <td>linkedin</td>
                    <td>
                      <a
                        href={apply.linkedin}
                        target="_blank"
                        rel="noreferrer"
                        className="text-blue-800 underline"
                      >
                        {apply.linkedin}
                      </a>
                    </td>
                  </tr>
                  <tr>
                    <td>github</td>
                    <td>
                      <a
                        href={apply.github}
                        target="_blank"
                        rel="noreferrer"
                        className="text-blue-800 underline"
                      >
                        {apply.github}
                      </a>
                    </td>
                  </tr>
                  <tr>
                    <td>custom</td>
                    <td>
                      <a
                        href={apply.custom}
                        target="_blank"
                        rel="noreferrer"
                        className="text-blue-800 underline"
                      >
                        {apply.custom}
                      </a>
                    </td>
                  </tr>
                  <tr>
                    <td>token</td>
                    <td>
                      <span className="text-sm bg-red-900 text-white px-1">
                        {apply.token}
                      </span>
                    </td>
                  </tr>
                  {/* <tr>
                    <td>quiz</td>
                    <td>
                      {loading ? (
                        <>loading...</>
                      ) : (
                        quizzes.length > 0 && <Quizzes quizzes={quizzes} />
                      )}
                    </td>
                  </tr> */}
                  <tr>
                    <td>video</td>
                    <td>
                      {loading ? (
                        <>loading...</>
                      ) : (
                        video && (
                          <div>
                            <a
                              href={`${BACKEND_PATH}/uploads/${video.filename}`}
                              target="_blank"
                              rel="noreferrer"
                              download={video.filename}
                              className="text-blue-700 font-semibold underline"
                            >
                              {video.filename}
                            </a>
                            <p>{video.size.toLocaleString()}B</p>
                            <p>{new Date(video.createdAt).toLocaleString()}</p>
                          </div>
                        )
                      )}
                    </td>
                  </tr>
                  {/* <tr>
                    <td>History</td>
                    <td>
                      {apply.history.map((item, index) => (
                        <HistoryItem key={index} history={item} />
                      ))}
                    </td>
                  </tr> */}
                </tbody>
              </table>
            </div>
          </>
        )
      )}
    </div>
  );
};

const Experience = ({ exp }) => {
  const [exparr, setExparr] = useState([]);

  useEffect(() => {
    let arr = [];
    if (exp) {
      for (let i in exp) {
        arr.push({ title: i, value: sliderToMonths(exp[i]) });
      }
    }
    setExparr(arr);
  }, [exp]);

  return (
    exparr.length > 0 && (
      <>
        <span>{exparr[0].title}</span>-<span>{exparr[0].value}</span>
      </>
    )
  );
};

