const HistoryItem = ({ history }) => {
  const [show, setShow] = useState(false);

  const handleClickShow = () => {
    setShow(!show);
  };

  return (
    <div className="text-sm border-b">
      <div
        className={clsx("underline decoration-dotted cursor-pointer", {
          "overflow-hidden max-w-[200px] truncate": !show,
        })}
        onClick={handleClickShow}
      >
        {history.status}
      </div>
      <div>{new Date(history.updatedAt).toLocaleString()}</div>
    </div>
  );
};
