import React, { useCallback, useEffect, useState } from "react";
import axios from "axios";
import { BACKEND_API_PATH } from "@config";
import { STEPS } from "@constants";
import useStore from "@store";
import { useSocket } from "@contexts/SocketContext";
import { Horizontal3DotsLoading } from "@assets/loader";
import ApplicationItem from "@home/ApplicationItem";

const Home = () => {
  const {
    applications,
    loadMoreApplications,
    newApplication,
    updateApplication,
    readApplication,
    readApplications,
  } = useStore();
  const socket = useSocket();
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState({
    search: "",
    country: "",
    lastStep: "",
    status: "",
    camera: "",
  });

  const handleChange = ({ target: { name, value } }) => {
    console.log(name, value);
    setSearch({ ...search, [name]: value });
  };

  const getApplications = useCallback(
    async (offset = 0) => {
      setLoading(true);
      try {
        const response = await axios.get(
          `${BACKEND_API_PATH}/applications?offset=${offset}`
        );
        loadMoreApplications(response.data.applications);
      } catch (error) {
        console.log(error);
      } finally {
        setLoading(false);
      }
    },
    [loadMoreApplications]
  );

  useEffect(() => {
    const timer = setTimeout(() => {
      getApplications();
    }, 300);
    return () => {
      if (timer) clearTimeout(timer);
    };
  }, [getApplications]);

  const handleLoadMore = useCallback(() => {
    if (!loading) getApplications(applications.length);
  }, [loading, applications, getApplications]);

  useEffect(() => {
    const timer = setTimeout(() => {
      if (socket) {
        socket.on("new-application", (message) => {
          newApplication(message);
        });
        socket.on("update-application", (message) => {
          updateApplication(message);
        });
        socket.on("read-application", (message) => {
          readApplication(message);
        });
        socket.on("read-applications", (message) => {
          readApplications(message);
        });

        return () => {
          socket.off("new-application");
          socket.off("read-application");
          socket.off("read-applications");
          socket.off("update-application");
        };
      }
    }, 300);
    return () => {
      if (timer) clearTimeout(timer);
    };
  }, [
    socket,
    newApplication,
    updateApplication,
    readApplication,
    readApplications,
  ]);

  return (
    <div className="p-8">
      <div className="min-h-[60vh] space-y-4">
        <div className="sticky top-0 z-10 py-2 flex justify-end">
          <div className="flex items-center justify-end flex-wrap gap-1 bg-[#f3f4f6]">
            <div className="max-w-60">
              <p className="text-xs font-medium text-gray-600 pl-2">Search</p>
              <input
                className="w-full bg-white rounded py-1 px-2 border border-[#aaa] outline-[#888]"
                placeholder="search name and email"
                name="search"
                value={search.search}
                onChange={handleChange}
              />
            </div>
            {/* <div>
              <p className="text-xs font-medium text-gray-600 pl-2">Country</p>
              <select className="bg-white rounded p-1 border border-[#aaa] outline-[#888]"
                name="country"
                value={search.country}
                onChange={handleChange}>
                <option value="">All</option>
                <option value="">United States</option>
              </select>
            </div> */}
            <div>
              <p className="text-xs font-medium text-gray-600 pl-2">
                Last Step
              </p>
              <select
                className="bg-white rounded p-1 border border-[#aaa] outline-[#888]"
                name="lastStep"
                value={search.lastStep}
                onChange={handleChange}
              >
                <option value="">All</option>
                {STEPS.map((item, index) => (
                  <option key={index} value={item}>
                    {item}
                  </option>
                ))}
              </select>
            </div>
            <div>
              <p className="text-xs font-medium text-gray-600 pl-2">Status</p>
              <select
                className="bg-white rounded p-1 border border-[#aaa] outline-[#888]"
                name="status"
                value={search.status}
                onChange={handleChange}
              >
                <option value={""}>All</option>
                <option value={1}>Viewed</option>
                <option value={2}>Copied</option>
              </select>
            </div>
            <div>
              <p className="text-xs font-medium text-gray-600 pl-2">
                Camera Status
              </p>
              <select
                className="bg-white rounded p-1 border border-[#aaa] outline-[#888]"
                name="camera"
                value={search.camera}
                onChange={handleChange}
              >
                <option value={""}>All</option>
                <option value={1}>Approved</option>
                <option value={0}>not Appr</option>
              </select>
            </div>
          </div>
        </div>
        {applications
          .filter((item) => {
            return (
              (item.user.fullName.indexOf(search.search) !== -1 ||
                item.user.email.indexOf(search.search) !== -1) &&
              (search.camera
                ? item.cameraStatus === Boolean(Number(search.camera))
                : true) &&
              (search.status
                ? item.status.toString() === search.status
                : true) &&
              (search.lastStep ? item.step === search.lastStep : true)
            );
          })
          .map((apply, index) => (
            <ApplicationItem apply={apply} key={apply._id} index={index} />
          ))}
        <div className="flex justify-stretch items-center">
          <div className="w-full h-[2px] rounded bg-gray-500"></div>
          <div
            className="px-4 text-nowrap cursor-pointer border border-gray-400 rounded-lg"
            onClick={handleLoadMore}
          >
            {loading ? <Horizontal3DotsLoading width={80} /> : "Load More"}
          </div>
          <div className="w-full h-[2px] rounded bg-gray-500"></div>
        </div>
      </div>
    </div>
  );
};

export default Home;
